<!-- eslint-disable -->
<template>
  <div class="equipa-wrapper">
    <div class="equipa movement">
      <div class="left">
        <div class="pic" :style="{ 'background-image': 'url(' + img + ')' }"></div>
        <div class="name">{{name}}</div>
        <div class="cargo">{{cargo}}</div>
      </div>
      <div class="right">
        <h1 class="title main">{{title}}</h1>
        <div class="quote">
          <div class="icon" :style="{ 'background-image': 'url(' + quote + ')' }"></div>
          <div class="txt" v-html="text"></div>
        </div>
        <router-link :to="url">
          <div class="subquote">Saiba mais</div>
          <img :src="arrow" alt />
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import quote from "../../assets/quote.png";
import axios from "axios";
import arrow from "../../assets/arrow.svg";

export default {
  data() {
    return {
      arrow,
      quote: quote,
      title: "",
      text: "",
      cta_txt: "",
      url: "",
      name: "",
      cargo: "",
      img: "",
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getEquipaHomepage.php")
      .then((resp) => {
        const data = resp.data;
        var src = data[0].img.split("uploads");

        vm.title = data[0].title;
        vm.text = data[0].text;
        vm.cta_txt = data[0].cta_txt;
        vm.url = data[0].link;

        vm.name = data[0].name;
        vm.cargo = data[0].cargo;
        vm.img = "https://beta.anacarolinapereira.pt/uploads" + src[1];
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.equipa-wrapper {
  background-color: $white;
  height: pxToEm(990);
  padding: $pd-desktop;
  display: flex;
  align-items: center;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  .equipa {
    padding: pxToEm(110) pxToEm(120);
    max-width: pxToEm(1010);
    max-height: pxToEm(350);
    width: 100%;
    height: 100%;
    box-shadow: pxToEm(10) pxToEm(10) pxToEm(50) rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media only screen and (min-width: 600px) and (max-width: 767px) {
      padding: pxToEm(75) pxToEm(75);
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      padding: pxToEm(35) pxToEm(40);
      box-shadow: pxToEm(5) pxToEm(5) pxToEm(30) rgba(0, 0, 0, 0.3);
      margin: pxToEm(40) 0;
      max-height: pxToEm(800);
      height: auto;
      flex-direction: column;
    }

    .left {
      display: flex;
      flex-direction: column;
      height: 100%;
      justify-content: space-between;
      align-items: center;
      margin-right: pxToEm(20);

      > .title {
        // font-size: pxToEm(34);
        color: #7d7d7d;
        display: none;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(10);
          display: block;
        }
      }

      .pic {
        border-radius: $circle;
        width: pxToEm(270);
        height: pxToEm(270);
        background-color: #cbcbcb;
        background-size: cover;

        @media only screen and (min-width: 600px) and (max-width: 767px) {
          width: pxToEm(200);
          height: pxToEm(200);
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: pxToEm(200);
          height: pxToEm(200);
        }
      }

      .name {
        color: $turquiose;
        font-size: pxToEm(24);
        font-weight: bold;
        margin-top: pxToEm(20);
        text-align: center;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(10);
        }
      }

      .cargo {
        text-transform: uppercase;
        font-size: pxToEm(16);
        color: #7d7d7d;
        margin-top: pxToEm(10);
      }
    }

    .right {
      width: pxToEm(710);
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      text-align: right;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        max-width: pxToEm(460);
        width: 100%;
        text-align: center;
      }

      .title {
        margin: 0;
        font-weight: normal;
        color: #7d7d7d;
        display: block;
        margin-bottom: pxToEm(15);
        font-size: pxToEm(38);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(10);
          display: none;
        }
      }

      .quote {
        display: flex;
        justify-content: space-between;
        font-family: "OS-Regular";

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(20);
        }

        .icon {
          width: pxToEm(16);
          height: pxToEm(15);
          background-size: cover;
        }

        .txt {
          max-width: pxToEm(710);
          width: 100%;
          color: #7b7b7b;
          font-size: pxToEm(18);
        }
      }

      a {
        display: flex;
        width: 17%;
        justify-content: space-between;
        align-self: flex-end;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: 30%;
          margin-top: pxToEm(20);
        }

        .subquote {
          font-weight: bold;
          font-size: pxToEm(20);
          color: $turquiose;
          text-transform: uppercase;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            // margin-top: pxToEm(20);
          }
        }

        .arrow-cont {
          position: absolute;
          transition: 0.5s;

          .arrow {
            border: solid $turquiose;
            border-width: 0 pxToEm(1) pxToEm(1) 0;
            display: inline-block;
            padding: pxToEm(2);
            position: absolute;
            top: pxToEm(-2);
            left: pxToEm(4);

            &.right {
              transform: rotate(-45deg);
              -webkit-transform: rotate(-45deg);
            }
          }
        }
      }
    }
  }
}
</style>



